.cellWrapper {
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
}

.cellIconStyle {
	height: 39px;
	width: 39px;
	border-radius: 5px !important;
	z-index: 1;
}

.cellButtonStyle {
	position: relative;
	cursor: pointer;
	border: none;
	border-radius: 10px;
	background-color: transparent;
	background-size: contain;
	font-size: 2rem;
	text-decoration: none;
	width: 100%;
	height: 100%;
	padding: 0;
	z-index: 1;
}

.cellButtonStyle:focus {
	outline: none;
	box-shadow: none;
}

.cellPointsStyle {
	background-color: #000;
	border: #333;
	border-radius: 3px !important;
	bottom: -5px;
	cursor: pointer;
	font-size: 11px;
	padding: 0 3px;
	position: absolute;
	right: -5px;
	user-select: none;
}

.tooltipStyle {
	left: 50px;
	bottom: 50px;
	min-width: 15rem;
	padding: 8px 4px 4px 9px;
	position: absolute;
	border: 1px solid black;
	z-index: 3;
}

@media only screen and (max-width: 500px) {
	.tooltipStyle {
		right: 1rem !important;
		left: 1rem !important;
		position: fixed;
	}
}

.tooltipHeader {
	display: flex;
	justify-content: space-between;
	margin-bottom: 0.5rem;
}

.tooltipTitle {
	font-size: 14px;
	line-height: 19px;
	color: white;
}

.tooltipActiveInfo {
	font-size: 12px;
	line-height: 17px;
	color: #fff;
}

.tooltipActiveRow {
	display: flex;
	justify-content: space-between;
}

.tooltipRank {
	color: #9d9d9d;
	font-size: 14px;
	line-height: 19px;
}

.tooltipNextRank {
	color: #fff;
	margin-top: 1rem;
}

.cellHighlight {
	position: absolute;
	height: 45px;
	width: 45px;
	z-index: 1;
}

.tooltipDescription {
	color: #ffd100;
	font-size: 12px;
	line-height: 17px;
}

.arrowR1Wrapper {
	position: absolute;
	z-index: 2;
	right: -15px;
	overflow: hidden;
}

.arrowR1 {
	z-index: 2;
	max-width: 100%;
