.changedBar {
	position: absolute;
	top: 2px;
	right: 10px;
	left: 10px;
	min-height: 10px;
	min-width: 20%;
	z-index: 2;
	display: flex;
	justify-content: space-around;
}

.changedIcon {
	height: 7px;
	width: 7px;
	background-color: #c41e3b;
	border-radius: 50%;
}

.changedIcon:nth-child(2) {
	background-color: #fff468;
}

.changedIcon:nth-child(3) {
	background-color: #2359ff;
}
