.tree {
	background: #444;
	border: 2px solid #444;
	border-radius: 6px;
	margin: 2px;
	display: inline-block;
	width: 240px;
}

.treeHeader {
	color: #fff;
	font-size: 16px;
	padding: 6px;
	position: relative;
	display: flex;
	align-items: center;
}

.treeHeaderHead {
	display: flex;
	align-items: center;
	margin: 0 auto;
}

.treeHeaderIcon {
	position: relative;
	margin-right: 0.5rem;
	height: 26px;
	width: 26px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.treeHeaderIconBorder {
	position: absolute;
	background-image: url(https://wow.zamimg.com/images/Icon/small/border/default.png);
	height: 26px;
	width: 26px;
}

.treeHeaderIconSpec {
	height: 18px;
	width: 18px;
}

.grid {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-template-rows: repeat(7, 1fr);
	height: 420px;
	width: 240px;
	background-size: 100% 100%;
	border-radius: 4px;
	position: relative;
}

.reset {
	position: absolute;
	right: 0.5rem;
	color: #c0392b;
	top: 50%;
	transform: translateY(-50%);
}

.reset:hover {
	cursor: pointer;
}
